.ArticleHead {
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-size:large;
}

.ArticleHead h4{
    font-weight: normal;
}

.md {
    width: 100%;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    text-align: left;
    word-break: break-all;
}

.md p {
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: larger;
}

.md h1, h2, h3, h4, h5, h6 {
    margin-left: 10px;
    margin-right: 10px;
}

.md a {
    color: blue;
}

.md a:hover {
    color: purple;
}

.md blockquote {
    margin-left: 10px;
    margin-right: 10px;
    padding: 10px;
    border-left:5px solid rgb(200, 200, 255);
    background-color: rgba(255, 255, 255, 0.4);
}

.md blockquote p {
    margin: 0px;
}

.md img{
    display: block;
    max-width: 80%;
    max-height: 75vh;
    margin: auto;
}

.md h1 {
    font-size: xx-large;
    padding: 10px;
    font-weight: normal;
    border-bottom: 2px solid rgb(228, 228, 228);
}

.md h2 {
    padding-left: 20px;
    font-size: xx-large;
    font-weight: normal;
}

.md h3 {
    padding-left: 20px;
    font-size: x-large;
    font-weight: normal;
}

.md h4 {
    padding-left: 30px;
    font-size: x-large;
    font-weight: normal;
}

.md h5 {
    padding-left: 40px;
    font-size: x-large;
    font-weight: normal;
}

.md h6 {
    padding-left: 50px;
    font-size: x-large;
    font-weight: normal;
}

.md code {
    background-color: rgba(200, 200, 255, 0.5);
}

.md pre {
    margin: 10px;
    background-color: rgb(255, 255, 255, 0.3);
    padding: 20px;
    overflow-x:auto;
}

.md pre code {
    background-color: rgba(0,0,0,0);
}

.md li {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 8px;
    list-style: none;
}

.md li::before {
    content: "•";
    transform: translateX(100px);
    font-weight: 900;
}

.md ol {
    padding-left: 30px;
    padding-right: 30px;
}

.md ul {
    padding-left: 30px;
    padding-right: 30px;
}